/* Section story start */
.section-stories {

}
.section-stories__story {

}
.section-stories__story__img {
    position: relative;
    background: var(--background-preloader);
    border: 5px solid var(--background-accent-sup);
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 15px;
}
.section-stories__story__img:before {
    content: '';
    padding-top: 100%;
    width: 0;
    display: block;
}
.section-stories__story__img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.section-stories__story__title {
    font-size: 1.25em;
    min-height: calc(var(--line-height-standart) * 1em);
    text-align: center;
}
.section-stories__story--preloader .section-stories__story__title {
    background: var(--background-preloader);
}

/* Section story end */