/* Section socials start */
.section-socials__list {
}
.section-socials__list-wrapper {
    display: flex;
    list-style: none;
    padding: 0;
    margin: -15px;
}
.section-socials__item {
    flex: 0 0;
    flex-basis: calc(33.3333% - 30px);
    margin: 15px;
}
.section-socials__item a {
    background: var(--background-accent);
    color: inherit;
    text-decoration: none;
    display: flex;
    align-items: flex-end;
    border-radius: 20px;
    padding: 35px 50px;
    font-size: 50px;
    line-height: .9;
    position: relative;
}
.section-socials__item a:before {
    content: '';
    display: block;
    width: 0;
    padding-top: 65%;
}
.section-socials__icon {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 25%;
}
.section-socials__icon:before {
    content: '';
    width: 0;
    display: block;
    padding-top: 100%;
}
.section-socials__icon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
/* Section socials end */
