.footer {
    background: var(--background-reverse);
    color: var(--text-reverse);
    margin-top: 50px;
}
.footer img, 
.footer svg {
    display: block;
}

.footer__container {
    padding-top: 55px;
    padding-bottom: 45px;
}

.footer__row-1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 100px;
    font-size: 1.25em;
}
.footer__row-1__logo {
    width: 200px;
}
.footer__row-1__logo svg {
    width: 100%;
    height: auto;
}
.footer__row-1__list {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 20px;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
}
.footer__row-1__item {
    margin-left: 50px;
}
.footer__row-1__item a {
    color: inherit;
    text-decoration: none;
    display: block;
    padding-top: 5px;
    padding-bottom: 5px;
}

.footer__row-2 {
    font-size: 1.25em;
    margin-bottom: 100px;
}
.footer__row-2 p {
    margin: 0;
}
.footer__row-2 p a {
    color: inherit;
    text-decoration: none;
}
.footer__row-2__list {
    list-style: none;
    margin-top: 15px;
    margin-bottom: 50px;
    padding-left: 0;
    display: flex;
}
.footer__row-2__item {
    margin-right: 18px;
}
.footer__row-2__item a {
    display: block;
    color: inherit;
    text-decoration: none;
}

.footer__row-3 {
    display: flex;
    justify-content: space-between;
}
.footer__row-3 p {
    margin: 0;
}
.footer__row-3 a {
    color: var(--text-accent-sup);
}