/* Section about start */
.section-about {

}
.section-about .container {
    display: flex;
}
.section-about__col-left {
    flex: 0 0;
    flex-basis: calc(50% - 15px);
    padding-right: 15px;
}
.section-about__col-right {
    flex: 0 0;
    flex-basis: calc(50% + 15px);
}
.section-about__col-right img {
    max-width: 100%;
}
.section-about .section__title {
    color: var(--text-accent);
    margin-bottom: 0;
}
.section-about p {
    font-size: 1.5em;
    margin: 1.25em 0 0;
}
.section-about p strong {
    color: var(--text-accent);
}
/* Section about end */