.header {
    position: relative;
    z-index: 1;
}

.header__container {
    display: flex;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
}

.header__logo {
    margin-right: 30px;
}

.header__logo img {
    display: block;
    height: 60px;
    width: auto;
}

ul.header__links {
    list-style: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    display: flex;
}

li.header__link {

}

li.header__link a {
    display: block;
    white-space: nowrap;
    color: inherit;
    text-decoration: none;
    line-height: 1;
    padding: 0.5em 1em;
    font-size: 20px;
    text-transform: lowercase;
}

.header__basket {
    margin-left: 80px;
}