/* Section catalog list start */
.section-catalog-list {

}
.section-catalog-list .section__title {
    margin-bottom: 20px;
}
.section-catalog-list__category {
    margin-bottom: 80px;
}
.section-catalog-list__category__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: -10px -35px;
}
.section-catalog-list__category__item {
    color: var(--text-gray-ligth);
    text-decoration: none;
    transition: color 0.25s;
    text-transform: lowercase;
    font-size: 1.875em;
    padding: 5px 15px;
    margin: 5px 20px;
}
.section-catalog-list__category__item.active {
    color: var(--text-accent); 
}
.section-catalog-list__category__item--preloader:before {
    content: '';
    display: block;
    height: calc(var(--line-height-standart) * 1em);
    background: var(--background-preloader);
}
.section-catalog-list__category__item--preloader:nth-child(3n + 1):before {
    width: 7em;
}
.section-catalog-list__category__item--preloader:nth-child(3n + 2):before {
    width: 2em;
}
.section-catalog-list__category__item--preloader:nth-child(3n + 3):before {
    width: 5em;
}
.section-catalog-list__category__item:hover {
    color: var(--text);
}
.section-catalog-list__list {

}
.section-catalog-list__list__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
}
.section-catalog-list__list__item {
    display: block;
    flex: 0 0;
    flex-basis: calc(33.333% - 30px);
    margin: 15px;
    color: inherit;
    text-decoration: none;
    position: relative;
}
.section-catalog-list__list__item--sale:before {
    content: 'sale';
    text-transform: uppercase;
    font-family: var(--font-sup);
    font-size: 30px;
    font-weight: 700;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: var(--background-accent);
    color: var(--text-reverse);
    border-radius: 50%;
    width: 2.8333em;
    height: 2.8333em;
    position: absolute;
    pointer-events: none;
    z-index: 1;
    top: 10px;
    right: 18px;
}
.section-catalog-list__list__img {
    position: relative;
    overflow: hidden;
    border-radius: 20px;
    margin-bottom: 10px;
    background: var(--background-preloader);
}
.section-catalog-list__list__img:before {
    content: '';
    display: block;
    width: 0;
    padding-top: 146%;
}
.section-catalog-list__list__img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.section-catalog-list__list__name {
    min-height: calc(var(--line-height-standart) * 1em);
    margin-bottom: 5px;
}
.section-catalog-list__list__item--preloader .section-catalog-list__list__name {
    background: var(--background-preloader);
    max-width: 15em;
}
.section-catalog-list__list__price {
    display: flex;
    flex-wrap: wrap;
    color: var(--text-gray);
}
.section-catalog-list__list__price__old {
    text-decoration: line-through;
    margin-right: 0.625em;
}
.section-catalog-list__list__price__current {
    min-height: calc(var(--line-height-standart) * 1em);
}
.section-catalog-list__list__item--preloader .section-catalog-list__list__price__current {
    background: var(--background-preloader);
    width: 100%;
    max-width: 8em;
}

.section-catalog-list__list__notice {
    text-align: center;
    flex: 1 1 100%;
    margin: 15px;
}
/* Section catalog list end */