/* Section Product Info start */
.section-product-info {

}

.section-product-info__back {
    font-size: 1.25em;
    line-height: 1;
    color: var(--text-gray-ligth);
    text-decoration: none;
    display: inline-block;
    margin-bottom: 30px;
}

.section-product-info__content {
    display: flex;
}
.section-product-info__left {
    flex: 0 0 58.33333%;
}

.section-product-info__left__carousel {
    position: relative;
    padding-left: 135px;
    padding-right: 35px;
}
.section-product-info__left__carousel .carousel__inner-slide img,
.section-product-info__left__carousel .carousel__thumbs__slide-wrapper img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.section-product-info__left__carousel .carousel__thumbs__slide-wrapper img {
    height: calc(100% - 3px);
}
.section-product-info__left__carousel .carousel__inner-slide img {
    border-radius: 20px;
}
.section-product-info__left__carousel .carousel__thumbs__slide-wrapper img {
    border-radius: 10px;
}
.section-product-info__left__carousel .carousel__thumbs {
    position: absolute;
    width: 90px;
    left: 0;
    top: 0;
}
.section-product-info__left__carousel .carousel__thumbs__slide {
    cursor: pointer;
}
.section-product-info__left__carousel .carousel__thumbs__slide:before {
    content: '';
    position: absolute;
    top: 0px;
    left: 0;
    bottom: 3px;
    right: 0;
    background: rgba(0, 0, 0, 0.42);
    border-radius: 10px;
    z-index: 1;
    transition: opacity 0.25s;
    opacity: 0;
    pointer-events: none;
}
.section-product-info__left__carousel .carousel__thumbs__slide--selected:before {
    opacity: 1;
}
.section-product-info__left__carousel .carousel__controls {
    margin-top: 15px;
}
.section-product-info__left__carousel .carousel__back-button {
    position: absolute;
    top: 50%;
    left: 100px;
}
.section-product-info__left__carousel .carousel__next-button {
    position: absolute;
    top: 50%;
    right: 0px;
}

.section-product-info__rigth {
    padding-left: 30px;
    flex: 0 0 41.66666%;
}
.section-product-info__title {
    font-size: 1.5625em;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 5px;
}
.section-product-info__price {
    display: flex;
    flex-wrap: wrap;
    font-size: 1.125em;
    color: var(--text-gray);
    margin-bottom: 30px;
}
.section-product-info__price__old {
    margin-right: 1em;
    text-decoration: line-through;
}
.section-product-info__toggle {
    margin-bottom: 30px;
}
.section-product-info__toggle + .section-product-info__toggle {
    margin-top: -15px;
}
.section-product-info__toggle__label {
    font-size: 0.875em;
    margin-bottom: 10px;
    text-transform: uppercase;
    color: var(--text-gray-dark);
}
.section-product-info__toggle__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: -5px;
}
.section-product-info__toggle__item {
    width: 36px;
    height: auto;
    background: none;
    border: 0;
    border-radius: 0;
    padding: 0;
    margin: 5px;
    cursor: pointer;
}
.section-product-info__toggle__item:disabled {
    cursor: not-allowed;
}
.section-product-info__toggle__inner:before {
    content: '';
    width: 0;
    display: block;
    padding-top: 100%;
}

.section-product-info__basket {
    cursor: pointer;
    padding: 0.6em 1em;
    border: 0;
    background: var(--background-accent);
    font-size: 20px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.section-product-info__basket svg {
    margin-left: 10px;
}

.section-product-info__dolyami {
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 0.75em;
}
.section-product-info__dolyami:before {
    content: 'Долями';
    padding: 0 0.666em;
    background: var(--background-accent-sup);
    color: var(--text-reverse);
    font-family: var(--font-sup);
    font-weight: 600;
    text-transform: uppercase;
    margin-right: 10px;
    height: 1.75em;
    display: flex;
    align-items: center;
    justify-content: center;
}

.section-product-info__desc {
    margin-top: 30px;
}
.section-product-info__desc__item {
    font-size: .875em;
    border-bottom: 1px solid var(--background-accent-sup);
    position: relative;
    cursor: pointer;
}
.section-product-info__desc__item + .section-product-info__desc__item {
    margin-top: 15px;
}
.section-product-info__desc__item svg {
    position: absolute;
    top: 3px;
    right: 0;
    transition: transform 0.25s;
}
.section-product-info__desc__item.section-product-info__desc__item--active svg {
    transform: rotate(180deg);
}
.section-product-info__desc__title {
    text-transform: uppercase;
    padding: 0.3em 24px 0.3em 0;
}
.section-product-info__desc__text {
    height: 0;
    overflow: hidden;
    transition: height 0.25s;
}
.section-product-info__desc__text p {
    margin-top: 10px;
    margin-bottom: 10px;
}
/* Section Product Info end */

/* Variation toggle items - sizes */
.section-product-info__sizes .section-product-info__toggle__item {
    color: var(--text-gray-ligth);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;    
}
.section-product-info__sizes .section-product-info__toggle__inner {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid var(--background-accent);
    color: var(--text-gray);
}
.section-product-info__sizes .section-product-info__toggle__info {
    font-size: 8px;
    pointer-events: none;
}
.section-product-info__sizes .section-product-info__toggle__item:disabled .section-product-info__toggle__inner {
    background: var(--text-gray-ligth);
    border-color: var(--text-gray-ligth);
}

/* Variation toggle items - color */
.section-product-info__color .section-product-info__toggle__inner {
    border-radius: 50%;
}