/* Section catalog start */
.section-catalog .carousel__inner-slide {
    background-color: var(--background-accent);
    border-radius: 20px;
    padding: 25px;
    overflow: hidden;
    z-index: 0;
}
.section-catalog .carousel__inner-slide a {
    color: inherit;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 100%;
}
.section-catalog__carousel-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    object-fit: cover;
    opacity: 0;
    transform: scale(1.1);
    transition: opacity 0.25s ease-in-out, transform 0.25s ease-in-out;
}
.section-catalog .carousel__inner-slide a:hover .section-catalog__carousel-image{
    opacity: 1;
    transform: scale(1);
}
.section-catalog__carousel-title {
    font-size: 50px;
    line-height: .9;
    text-transform: lowercase;
    max-width: calc(100% - 25% - 9px - 15px);
    word-break: break-all;
}
.section-catalog__carousel-icon {
    position: absolute;
    top: 34px;
    right: 34px;
    width: 25%;
}
.section-catalog__carousel-icon:before {
    content: '';
    display: block;
    width: 0;
    padding-top: 100%;
}
.section-catalog__carousel-icon svg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}
/* Section catalog end */