/* Section banner start */
.section-banner {
    position: relative;
    z-index: 0;
}
.section-banner__decor {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}
.section-banner__content {
    position: relative;
    width: 60%;
    margin-left: auto;
    max-width: 950px;
    margin-top: 30px;
    display: block;
    color: inherit;
    text-decoration: none;
}
.section-banner__content:before {
    content: '';
    width: 0;
    display: block;
    padding-top: 94%;
}
.section-banner__content__decor {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.section-banner__content__title {
    position: absolute;
    top: 58%;
    left: 50%;
    font-size: 70px;
    text-transform: uppercase;
    font-family: var(--font-accent);
    transform: translate(-50%, 0);
}
.section-banner__content__title svg {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(35%, -35%);
    width: 96px;
    height: 96px;
}
/* Section banner end */